import wallet from "@/common/js/wallet.js";
import tkiQrcode from "@/components/tki-qrcode.vue"
export default {
	components: {
		tkiQrcode
	},
	data() {
		return {
			wallet: {}
		}
	},
	onLoad() {
		uni.showLoading({
		    title: '二维码生成中'
		});
		this.wallet = this.$Wallet.getCurrentWallet();
		setTimeout(() => {
			uni.hideLoading();
		},500);
	},
	methods: {
		copy(){
			let self = this;
			uni.setClipboardData({
				data: self.wallet.address,
				success: function () {
					uni.showToast({  
					    title: '复制成功',  
					    mask: false,  
					    duration: 1500  
					});  
				}
			});
		}
	}
}
